@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
body {
  margin: 0;
  font-family: "Raleway", serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-image: linear-gradient(to left, rgb(27 20 41), rgb(20 15 35)); */
}

.top {
  position: fixed;
  transition: all 0.3s ease-out 0s;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

.sticky {
  background-color: #212038d8;
  transition: all 0.3s ease-out 0s;
  box-shadow: 0px 10px 10px 0px rgba(9, 5, 29, 0.171);
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

.navbar-toggler {
  padding: 0.25rem 1.5rem;
  position: relative;
  background-color: transparent;
  border-color: transparent;
  color: whitesmoke !important;
}

.navbar-toggler span {
  display: block;
  background-color: #99949c;
  height: 4px;
  width: 27px;
  margin-top: 5px;
  margin-bottom: 5px;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  left: 0;
  opacity: 1;
}

.navbar-brand {
  color: whitesmoke !important;
}

.navbar-nav .nav-link {
  color: whitesmoke !important;
  padding-right: 1rem;
  padding-left: 1rem;
}

.nav-link {
  padding: 0.8rem 1rem;
}

.navbar-nav .nav-item {
  position: relative;
  margin-left: 20px;
}

.navbar-nav .nav-item a {
  font-weight: 400;
  transition: all 0.3s ease-out 0s;
  position: relative;
  z-index: 1;
}

.navbar-nav .nav-item a::after {
  content: "";
  position: relative;
  display: block;
  height: 5px;
  width: 0;
  border-radius: 16px;
  background: #c95bf5;
  bottom: 1px;
  left: 0;
  z-index: -1;
  transition: all 0.3s ease-out 0s;
}

.navbar-nav .nav-item a:hover::after {
  width: 100%;
}

.nav-socials {
  display: flex;
}

.nav-socials svg {
  width : 1.5em;
  height: 100%;
}

@media (max-width: 767px) {
  .top {
    background-color: #212038d8;
  }

  .nav-item {
    text-align: center;
    margin: 0 !important;
  }

  .nav-link {
    padding: 0.7rem 1rem;
  }
  
  .nav-socials {
    justify-content: center;
  }

  .nav-socials a {
    margin: 0em 1em;
  }

  .nav-socials svg {
    width : 2em;
    height: 100%;
  }
}

.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height:83.8vh;
}

.home-front {
    text-align: center;
    
}

.home-text {
    margin-bottom: 1em;
}

.home-front button {
    display:inline-block;
    padding: 0.8em 3em;
    border:0.16em solid #000000;
    border-radius: 15px;
    margin:0 0.3em 0.3em 0;
    box-sizing: border-box;
    text-decoration:none;
    text-transform:uppercase;
    font-family:'Roboto',sans-serif;
    font-weight:400;
    text-align:center;
    transition: all 0.15s;
    background-color: #e1e0f3d8;
    color: #212038d8;
}

.home-front button:hover {
    transition: all 0.3s;
    color:black;
    border-color:#212038d8;
    background-color: #a7a6bbd8;
}
.about-skills {
    display: flex;
    flex-wrap: wrap;
    text-align:center;
    justify-content: center;
    margin: 0em 5em;
}

.skills-section {
    display:flex;
    width: 100%;
    justify-content: center;
    text-align: center;
}

.skill-icon {
    margin: 1em;
    padding: 1em 2em 0em 2em;
    opacity: 0.85;
    border: 1.7px solid whitesmoke;
    vertical-align: middle;
    text-align: center;
    border-radius: 10px;
    box-shadow: 4px 5px 4px 3px rgba(89, 4, 168, 0.137);
    transition: all 0.4s ease 0s;
}

.icon-img {
    width:70px;
    height:70px;
    color:whitesmoke;
    opacity: 0.7;
    transition: all 0.4s ease 0s;
}

.skill-icon-desc {
    color:black;
}

.skill-icon:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 1;
    border: 2.2px solid rgba(197, 115, 230, 0.883);
}

.skill-icon:hover .skill-icon-desc {
    transition: all 0.4s ease 0s;
    color: whitesmoke;
}

.skill-icon:hover .icon-img {
    opacity: 1;
}

.skill-img {
    max-height:100%;
    max-width:100%;
}

#expressicon {
    -webkit-filter: invert(1);
            filter: invert(1)
}

@media (max-width: 767px) {
    .skill-icon-desc {
        color: whitesmoke;
        font-size: 0.7rem;
    }

    .about-skills {
        margin: 0em 2em;
    }

    .skill-icon{
        opacity: 1;
    }

    .icon-img {
        opacity: 1;
    }
}
.about-container {
    min-height: 83.8vh;
}

.about-main {
    position: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.about-header {
    margin: 1em
}

.about-section {
    margin: 1em 10em;
}

.about-contact {
    display: flex;
    justify-content: center;
    width:100%;
}

.about-contact p {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.5rem 3rem;
}

.about-contact a {
    text-decoration: none;
    color: ivory;
    transition: all 0.4s ease 0s;
}

.about-contact a:hover {
    color: rgba(197, 115, 230, 0.883);
}

@media (max-width: 767px) {
    .about-section {
        margin: 1em 2em;
    }
}

@media (max-width: 678px) {
    .about-contact {
        flex-direction: column;
    }
}
.projects-container {
    display: flex;
    flex-direction: column;
    min-height:83.8vh;
    padding: 0 5em;
    text-align: center;
}

.projects-container ul{
    list-style: none;
}

.project-header {
    margin: 1em;
}

.filter-card {
    margin-bottom: 1em;
}

.filter-menu{
    margin-bottom: 20px;
    padding-left:0;
}
.filter-menu li{
    display: inline-block;
    padding: 10px 18px;
    border: 1px solid  	#212038d8;
    background: #647c90;
    color: ivory;
    cursor: pointer;
}
.filter-menu li:hover,
.filter-menu li.active{
    background: #212038d8;
}

.card-container {
    padding: 5px;
}

.card-container li{
    width: 33%;
    padding: 5px;
    float: left;
}

.card-container li.shown{
    width: 33%;
    padding: 5px;
    transition: all 0.5s ease;
}

.card-container li.hide{
    width: 0%;
    padding: 0;
    visibility:hidden;
    transition: all 0.5s ease;
}

.card-container img{
    display: block;
    width: 100%;
    height: 100%;
}

.portfolio-wrapper {
    overflow:hidden;
    position: relative;
    background: #666;
    cursor:pointer;
}

.portfolio img {
    display:block;
    max-width:100%;
    position: relative;
    top:0;
    transition: all 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
    -webkit-filter: brightness(70%);
    filter: brightness(70%);
}

.portfolio .label {
    position: absolute;
    width: 100%;
    height:100%;
    bottom:-100%;
    transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.portfolio .label-bg {
    background: #647c90;
    width: 100%;
    height:100%;
    position: absolute;
    top:0;
    left:0;
}

.portfolio .label-text {
    color:#dcd2cc;
    position: relative;
    z-index: 2;
    padding:5px 0px;
    height:100%;
    border: 1px solid black;
    cursor:auto;
}
        
.portfolio .text-category {
    display:block;
    font-size:9px;
}

.portfolio img:hover {
    -webkit-filter: brightness(100%);
    filter: brightness(100%);
}

.label-exit {
    position:absolute;
    top: 10px;
    right: 10px;
}

.text-title {
    font-size: 120%;
    color: black;
    -webkit-text-decoration: underline 2px;
            text-decoration: underline 2px;
}

.text-links {
    position:absolute;
    bottom: 0;
    width:100%;
    height:15%;
    background: #212038d8;
}

.link-img {
    height:100%;
    width:10%;
    margin: 0rem 1rem 0rem 1rem;
    padding: 5px;
    color: ivory;
}

.card-label {
    padding: 0.5em 1em;
    background-color: #212038d8;
    color:ivory;
}

.label-title {
    text-decoration: underline;
    font-size: 120%;
}

.text-content {
    padding: 2px 10px;
    text-align: #dcd2cc;
}

.text-content p{
    margin: 0.2em 1em;
    color:black;
    font-size:90%;
}

@media (max-width: 1280px){
    .portfolio img {
        -webkit-filter: brightness(100%);
        filter: brightness(100%);
    }
    .projects-container {
        padding: 0em 2em;
    }
    .card-container li.shown,
    .card-container li{
        width: 50%;
    }

    h2{
        font-size: 270%;
    }

    .text-content p {
        font-size: 100%;
    }
}

@media (max-width: 700px){
    .portfolio img {
        -webkit-filter: brightness(100%);
        filter: brightness(100%);
    }
    .projects-container {
        padding: 0em 1em;
    }

    .card-container li.shown,
    .card-container li{
        width: 100%;
    }

    h2{
        font-size: 190%;
    }

    .text-content p {
        font-size: 100%;
    }
}


html {
  margin-top: 72px;
}

h1, h2, h3, h4, p, a{
  color: ivory;
}

/* background */

body {
  background-color: black !important;
  background-image: url(data:image/png;base64,), url(/Portfolio/static/media/stars.90c266f7.png) !important;
  background-repeat: repeat, repeat;
  background-position-x: center, center;
  background-position-y: top, top;
  -webkit-animation: twinkle 200s linear infinite;
          animation: twinkle 200s linear infinite;
}

@-webkit-keyframes twinkle {
  100% {
    background-position: -5000px 100%, 3000px 5000px;
  }
}

@keyframes twinkle {
  100% {
    background-position: -5000px 100%, 3000px 5000px;
  }
}


/* fade */
.fade-appear,
.fade-enter {
  opacity: 0 !important;
  z-index: 1 !important;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1 !important;
  transition: opacity 300ms linear !important;
}

.fade-exit {
  opacity: 1 !important;
}

.fade-exit.fade-exit-active {
  opacity: 0 !important;
  transition: opacity 300ms linear !important;
}



